om the airport to the university, so you can’t get lost. I should add that the rules of the road are not applied with the same rigour you may be used to, so it is worth making sure you have full insurance on your car! Once in town there’s plenty of free, roadside parking. Once in Ifrane, pretty much everything is in walking distance. But there is a taxi rank right outside the university campus to take you anywhere you need to go. ")])])])])])}]

